import './App.css';
import Nav from './components/Nav';
import Welcome from './components/Welcome';
import { useState } from 'react';


function App() {
  const [Check, setCheck] = useState("off");
  const [stay, setstay] = useState(false);
  const toggleMode = () => {
    if (Check === "off"){
      setCheck("on");
      document.body.style.background = "grey"
      setstay(true);
      setTimeout(() => {
        setstay(false);
      }, 2000);
    }
    else{
      setstay(true);
      setCheck("off");
      setTimeout(() => {
        setstay(false);
      }, 2000);
      document.body.style.background = "white"
    }
  }
  return (
    <>
      <Nav title="Title" stay={stay} Check={Check} toggleMode={toggleMode}/>
      <Welcome/>
    </>
  );
}

export default App;